
export const login = (value1, value2) => {
    return (dispatch) => {
        dispatch({
            type: "login",
            payload1: value1,
            payload2: value2,




        })
    }
}

export const signup = (value1, value2,value3) => {
    return (dispatch) => {
        dispatch({
            type: "signup",
            payload1: value1,
            payload2: value2,
            payload3: value3,

        })
    }

}