
const Signup_Redusers = async(state="",action)=>{  
    if (action.type === "signup") {
       const Name = action.payload1[0]      
       const Email = action.payload2[0]      
       const Password = action.payload3[0]     
       var response = await fetch("http://localhost:5000/api/auth", {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
  
       body:JSON.stringify({Name,Email,Password})
      }); 

      const json = await response.json()

      return json
        
     }
     else {
        return state
     }
  
  }

export default Signup_Redusers