import React, { useState } from "react";
import { NavLink } from 'react-router-dom'
import { useSelector } from "react-redux";
import { useEffect } from "react";
import '../index.css'
const Menu = () => {
  const state = useSelector(state => state.login_check)
  const [page,setpage ]= useState(false)

  state.then((data)=>{
    if(data === true){
    
    setpage(data)}
  else{
    setpage(false)
  }
  })


  return <>
    <nav className="navbar">
      <div className="logo">I-NoteBook</div>
      <ul className="nav-links">
        <div className="menu">
         {page=== false?<>
          <li><NavLink exact activeClassName="active_op" to="/">Login</NavLink></li>
          <li>  <NavLink exact activeClassName="active_op" to="/signup">Signup</NavLink></li></>:""}
          {page=== true?<><li><NavLink exact activeClassName="active_op" to="/home">Home</NavLink></li><li>  <NavLink exact activeClassName="active_op" to="/about">About</NavLink></li>
          <li><NavLink exact activeClassName="active_op" to="/contact">Contact us</NavLink></li><li>
          <NavLink exact activeClassName="active_op" to="/logout">Logout</NavLink></li>
       </> :""}


        </div>
      </ul>
    </nav>
  </>
}

export default Menu;