import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router'
import { actioncreator } from '../store/index'
import Home from './Home'

const Login = () => {
  const location = useHistory()
  const dispatch = useDispatch()
  const state = useSelector(state => state.login_check)
 
  const [input, setinput] = useState(0)
  const [credials, update_credials] = useState({ email: "", password: "" })
  const handle_submit = (e) => {
    e.preventDefault()

    dispatch(actioncreator.login(credials.email, credials.password))
    setinput(input + 1)


  }
  const onchange_handle = (e) => {

    update_credials({ ...credials, [e.target.name]: [e.target.value] })
  }
  useEffect(() => {
    dispatch(actioncreator.signup("a","b","d"))
    state.then((data) => {
      if (data) {
        location.push("/home")
      }
    })
  }, [input])

  return (<>
    <div className="container text-center" >
      <div className="login_box">
        <form onSubmit={handle_submit}>
          <div className="mb-3" style={{ float: "left" }}>
            <label htmlFor="email" className="form-label">Email Address</label>
          </div>
          <div className="mb-3">
            <input type="email" className="form-control" id="email" name="email" aria-describedby="emailHelp" onChange={onchange_handle} />
            <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
          </div>
          <div className="mb-3" style={{ float: "left" }}>
            <label htmlFor="password" className="form-label">Password</label>
          </div>
          <div className="mb-3">
            <input type="password" className="form-control" id="password" name="password" onChange={onchange_handle} />
          </div>


          <button type="submit" className="btn btn-primary" >Login</button>

        </form>
      </div>

    </div>

  </>
  )
}

export default Login
