import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router'
import { actioncreator } from '../store'


function Signup() {
    const location = useHistory()
    const [input, setinput] = useState(0)
    const [perror, setperror] = useState(false)
    const state = useSelector(state => state.signup)
    const dispatch = useDispatch()
    const [credials, update_credials] = useState({ name: "", email: "", password: "", cpassword: "" })
    const handle_submit = (e) => {
        e.preventDefault()

        if (credials.cpassword[0] == credials.password[0]) {
            setinput(input + 1)
            setperror(false)
            dispatch(actioncreator.signup(credials.name, credials.email, credials.password))


        } else {
            setperror(true)  }
    }
    const onchange_handle = (e) => {

        update_credials({ ...credials, [e.target.name]: [e.target.value] })
    }
    useEffect(() => {
        state.then((data) => {
            if (data.success === true) {
                location.push("/")
            }
            else {
                console.log(data.errors)
            }
        })
    }, [input])
    return (
        <>
            <div className="container text-center" >
                <div className="signup_box">
                    <form onSubmit={handle_submit}>
                        <div className="mb-3" style={{ float: "left" }}>
                            <label htmlFor="name" className="form-label">Enter Your Name:</label>
                        </div>
                        <div className="mb-3">
                            <input type="text" className="form-control" id="name" name="name" onChange={onchange_handle} />
                        </div>
                        <div className="mb-3" style={{ float: "left" }}>
                            <label htmlFor="email" className="form-label">Enter Your Email:</label>
                        </div>
                        <div className="mb-3">
                            <input type="email" className="form-control" id="email" name="email" aria-describedby="emailHelp" onChange={onchange_handle} />
                            <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                        </div>
                        <div className="mb-3" style={{ float: "left" }}>
                            <label htmlFor="password" className="form-label">Password</label>
                        </div>
                        <div className="mb-3">
                            <input type="password" className="form-control" id="password" name="password" onChange={onchange_handle} />
                        </div>

                        <div className="mb-3" style={{ float: "left" }}>
                            <label htmlFor="cpassword" className="form-label">Confirm Password</label>
                        </div>
                        <div className="mb-3">
                            <input type="password" className="form-control" id="cpassword" name="cpassword" onChange={onchange_handle} />
                        </div>
{perror === true?   <div id="emailHelp" className="form-text">Your Password And Confirm Password Is Not Same</div>:""}
<div className="mb-3">
                        <button type="submit" className="btn btn-primary" >Login</button>
</div>
                    </form>
                </div>

            </div>

        </>
    )
}

export default Signup
