
const  Login_Redusers = async (state = "", action) => {
 
   if (action.type === "login") {
      const Email = action.payload1[0]
      const Password = action.payload2[0]
      var response = await fetch("http://localhost:5000/api/auth/login", {
         method: 'POST',
         headers: {
           'Content-Type': 'application/json',
         },
   
        body:JSON.stringify({Password,Email})
       });
       const json =await response.json()
      
    
        
       return json.success
         
       
    
      
    
      
   }
   else {
      return state
   }

}

export default Login_Redusers